import React from 'react';
import CommentsCell from './CommentsCell';


let counter = 0;

function createData(name, desc, image) {
  counter += 1;
  return {id: counter, name, desc, image};
}

const data = [
  createData('John Smith has a new request submission', 'I would like to publish my book entitle ABC.  :)', "https://via.placeholder.com/150x150", '45'),
  createData('Alex Dolgove has a new request submission', 'I would like to publish my book entitle ABC.  :)', "https://via.placeholder.com/150x150", '45'),
  createData('John Doe has a new request submission', 'I would like to publish my book entitle ABC.  :)', "https://via.placeholder.com/150x150", '45'),
];

const PopularAuthorsTable = () => {

  return (

    <div className="jr-comments">
      {data.map(data => {
        return (
          <CommentsCell key={data.id} data={data}/>
        );
      })}
    </div>
  );
};

export default PopularAuthorsTable;
